@include font("Bebas", bebas);
@include font("Helveticaneue Regular", helveticaneue-regular);
@include font("Helveticaneue Light", helveticaneue-light);
@include font("Helveticaneue Medium", helveticaneue-medium);
@include font("Helveticaneue Bold", helveticaneue-bold);
@include font("Helveticaneue Italic", helveticaneue-italic);
@include font("HelveticaneueLTStd-md", helveticaneueltstd-md);
@include font("HelveticaneueLTStd Roman", helveticaneueltstd-roman);

@font-face {
	font-family: "HelveticaNeueLTStd Hv";
	src: url(../fonts/HelveticaNeueLTStd-Hv) format ('truetype');
	font-weight: normal;
	font-style: normal; }

// mixins for fonts using
@mixin bebas {
	font-family: "Bebas"; }

@mixin helvReg {
	font-family: "Helveticaneue Regular"; }

@mixin helvLight {
	font-family: "Helveticaneue Light"; }

@mixin helvMedium {
	font-family: "Helveticaneue Medium"; }

@mixin helvBold {
	font-family: "Helveticaneue Bold"; }

@mixin helvItalic {
	font-family: "Helveticaneue Italic"; }

@mixin helvLSTmd {
	font-family: "HelveticaneueLTStd-md"; }

@mixin helvLSTroman {
	font-family: "HelveticaneueLTStd Roman"; }

@mixin helvLSThv {
	font-family: "HelveticaNeueLTStd Hv"; }

