.out {
	background: #fff url(img/bg.jpg) repeat; }
.header {
	max-width: 960px;
	margin: 0 auto;
	padding: 25px 10px 15px;
	display: flex;
	justify-content: space-between; }
.sections {
	max-width: 960px;
	margin: 0 auto;
	padding: 10px;
	display: flex;
	justify-content: space-between;
	@include r(1100) {
		margin-left: 75px; }
	@include r(1035) {
		margin: 0 auto;
		padding-left: 10px; }
	@include r(960) {
		display: block; } }




.main-section {
	width: 72%;
	@include r(960) {
		margin: 0 auto;
		width: 660px; }
	@include r(670) {
		width: 100%; } }

.aside-section {
	margin-top: 15px;
	width: 23%;
	@include r(960) {
		display: flex;
		width: 660px;
		margin: 0 auto;
		justify-content: space-between; }
	@include r(670) {
		width: 100%;
		flex-direction: column;
		align-items: center; } }
