.aside-bar {
	margin-bottom: 35px;
	@include r(960) {
		width: 30%; }
	@include r(670) {
		width: 90%; }
	&__content {
		margin-top: 13px;
		margin-left: -2px;
		color: #464646;
		@include helvLSTroman;
		font-size: 12px; } }

.aside-title {
	display: flex;
	&__heading {
		color: $main_decor;
		@include bebas;
		font-size: 14px;
		padding-right: 15px; }
	&__decor {
		margin-top: 5px;
		flex: 1 1 auto;
		height: 14px;
		background: transparent url(../img/end-line.png); } }

.categoris {
	&__link {
		color: #464646; } }
.photos {
	margin-left: -5.5px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	&__item {
		display: block;
		margin-left: 5.5px;
		margin-bottom: 6px;
		width: 50px;
		height: 50px; } }


