.out {
  background: #fff url(img/bg.jpg) repeat;
}

.header {
  max-width: 960px;
  margin: 0 auto;
  padding: 25px 10px 15px;
  display: flex;
  justify-content: space-between;
}

.sections {
  max-width: 960px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 1100px) {
  .sections {
    margin-left: 75px;
  }
}

@media only screen and (max-width: 1035px) {
  .sections {
    margin: 0 auto;
    padding-left: 10px;
  }
}

@media only screen and (max-width: 960px) {
  .sections {
    display: block;
  }
}

.main-section {
  width: 72%;
}

@media only screen and (max-width: 960px) {
  .main-section {
    margin: 0 auto;
    width: 660px;
  }
}

@media only screen and (max-width: 670px) {
  .main-section {
    width: 100%;
  }
}

.aside-section {
  margin-top: 15px;
  width: 23%;
}

@media only screen and (max-width: 960px) {
  .aside-section {
    display: flex;
    width: 660px;
    margin: 0 auto;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 670px) {
  .aside-section {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}

.logo {
  font-family: "Bebas";
  font-size: 18px;
  letter-spacing: -0.6px;
}

.logo__start {
  color: #e8603c;
}

.logo__end {
  color: #f2ae9c;
}

.navigation {
  display: flex;
  margin-top: 10px;
}

@media only screen and (max-width: 670px) {
  .navigation {
    display: none;
  }
}

.navigation__item {
  font-family: "Bebas";
  color: #B9B9B9;
  text-transform: uppercase;
  padding-left: 27px;
  letter-spacing: 0.05px;
}

.navigation__item_is-active {
  color: #e8603c;
}

.navigation__item:hover {
  color: #f2ae9c;
}

.menu-small {
  width: 40px;
  height: 40px;
  background: transparent url(../img/menu.png) no-repeat;
  background-size: cover;
  margin-right: 15px;
  display: none;
  z-index: 1;
}

.menu-small:hover, .menu-small_is-active {
  background-image: url(../img/menu_hover.png);
}

@media only screen and (max-width: 670px) {
  .menu-small {
    display: block;
  }
}

.navigation-small {
  position: absolute;
  width: 100%;
  display: none;
}

.navigation-small__item {
  width: 100%;
  align-items: center;
  background-color: #fff;
  padding: 10px;
}

.menu_link {
  font-family: "Bebas";
  color: #e8603c;
  padding: 10px;
}

.title {
  display: flex;
}

.title__left, .title__right {
  background-color: #f2ae9c;
  flex: 1 1 auto;
}

@media only screen and (max-width: 1100px) {
  .title__left {
    flex: 0 1 75px;
  }
}

@media only screen and (max-width: 1035px) {
  .title__left {
    flex-basis: auto;
  }
}

.title__center {
  background-color: #e8603c;
  flex-basis: 940px;
  padding: 22px 20px 16px;
  font-family: "Bebas";
  color: #fff;
  text-transform: uppercase;
  margin: 0 10px;
  font-size: 24px;
}

@media only screen and (max-width: 1035px) {
  .title__center {
    flex-basis: 100%;
  }
}

.frame_first {
  margin-top: 14px;
}

.frame_media {
  margin-top: 57px;
}

.frame_quotes {
  margin-top: 47px;
}

.frame_link {
  margin-top: 45px;
}

.frame__icon {
  display: block;
  width: 75px;
  height: 75px;
  margin-left: -85px;
}

@media only screen and (max-width: 1035px) {
  .frame__icon {
    display: none;
  }
}

.frame__icon_slider {
  background: #e8603c url(../img/icon_slider.png) no-repeat center center;
}

.frame__icon_img {
  background: #e8603c url(../img/icon_img.png) no-repeat center center;
}

.frame__icon_video {
  background: #e8603c url(../img/icon_video.png) no-repeat center center;
}

.frame__icon_quotes {
  background: #e8603c url(../img/icon_quotes.png) no-repeat center center;
}

.frame__icon_link {
  background: #e8603c url(../img/icon_link.png) no-repeat center center;
}

.frame__end-line {
  height: 14px;
  background: #fff url(../img/end-line.png) repeat-x;
  margin-top: 45px;
  margin-right: 15px;
}

.slider {
  margin-bottom: 22px;
}

@media only screen and (max-width: 900px) {
  .slider__img {
    width: 100%;
  }
}

.article {
  margin-top: -75px;
}

@media only screen and (max-width: 1035px) {
  .article {
    margin-top: 0;
  }
}

.article__text {
  display: flex;
}

@media only screen and (max-width: 670px) {
  .article__text {
    display: block;
  }
}

.short-info {
  margin-top: -5px;
  font-size: 12px;
  flex: 0 0 140px;
  padding-left: 2px;
  line-height: 20px;
}

@media only screen and (max-width: 670px) {
  .short-info {
    display: flex;
    justify-content: space-around;
    margin-bottom: 15px;
  }
}

.short-info__heading {
  font-family: "Bebas";
  color: #e8603c;
}

.short-info__text {
  color: #bababa;
  font-family: "Helveticaneue Light";
  letter-spacing: 0.2px;
  margin-bottom: 10px;
}

.tag {
  color: #bababa;
  font-family: "Helveticaneue Light";
  letter-spacing: 0.2px;
  padding-right: 3px;
}

.tag:not(:first-child):before {
  content: "|";
  padding-right: 6px;
}

.tag:hover {
  text-decoration: underline;
}

.content {
  margin-left: 6px;
  margin-bottom: 2px;
  margin-right: 15px;
  flex: 1 1 auto;
  letter-spacing: -0.25px;
}

.content__heading {
  display: block;
  font-family: "Bebas";
  color: #e8603c;
}

.content__paragraph {
  color: #414141;
  font-family: "Helveticaneue Medium";
  font-size: 12px;
  margin-top: 17px;
  margin-left: 3px;
  line-height: 1.5;
}

.content__paragraph:first-of-type {
  margin-top: 14px;
}

.article-quotes {
  margin-bottom: 54px;
  padding: 20px 10px 20px 35px;
  min-height: 75px;
  background: #f1f1f1 url(../img/bg_quotes.png);
}

.article-quotes__quote {
  padding-left: 42px;
  background: transparent url(../img/quotes.png) no-repeat;
  color: #474747;
  font-family: "Helveticaneue Italic";
  font-size: 13px;
  line-height: 18px;
}

.article-link__heading {
  color: #e8603c;
  font-family: "Bebas";
  font-size: 18px;
  letter-spacing: 0.35px;
}

.article-link__link {
  color: #a0a0a0;
  font-family: "HelveticaneueLTStd Roman";
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.1px;
  margin-left: 2px;
}

.article-link__text {
  margin-top: 19px;
  margin-left: 2px;
  color: #414141;
  font-family: "HelveticaneueLTStd Roman";
  font-size: 12px;
  line-height: 18px;
}

.pagination {
  display: flex;
  margin: 42px 0 50px;
  padding-left: 3px;
}

.pagination__item {
  width: 26px;
  height: 26px;
  display: block;
  background-color: #f2ae9c;
  margin-right: 4px;
  padding: 7px 10px 4px;
  color: #ffffff;
  font-family: "Bebas";
  font-size: 14px;
}

.pagination__item_is-active {
  background-color: #e8603c;
}

.aside-bar {
  margin-bottom: 35px;
}

@media only screen and (max-width: 960px) {
  .aside-bar {
    width: 30%;
  }
}

@media only screen and (max-width: 670px) {
  .aside-bar {
    width: 90%;
  }
}

.aside-bar__content {
  margin-top: 13px;
  margin-left: -2px;
  color: #464646;
  font-family: "HelveticaneueLTStd Roman";
  font-size: 12px;
}

.aside-title {
  display: flex;
}

.aside-title__heading {
  color: #e8603c;
  font-family: "Bebas";
  font-size: 14px;
  padding-right: 15px;
}

.aside-title__decor {
  margin-top: 5px;
  flex: 1 1 auto;
  height: 14px;
  background: transparent url(../img/end-line.png);
}

.categoris__link {
  color: #464646;
}

.photos {
  margin-left: -5.5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.photos__item {
  display: block;
  margin-left: 5.5px;
  margin-bottom: 6px;
  width: 50px;
  height: 50px;
}
